import { timingRangeSlice } from './rangeSlice';
import { coloringSlice } from './colorSlice';
import { pulsesSlice } from './pulsesSlice';
import { runtimeSlice } from './runtimeSlice';
import { selectionSlice } from './selectionSlice';
import { layoutSlice } from './layoutSlice';
import { popupSlice } from './popupSlice';
import { highlightSlice } from './highlightSlice';
import { hoverSlice } from './hoverSlice';
// Since we are using a number of pulses defined here with the rest of the global redux state
// we need a few custom functions to combine these reducers and state types, to integrate
// them into the rest of the redux state.
const slices = [
    pulsesSlice,
    runtimeSlice,
    selectionSlice,
    layoutSlice,
    popupSlice,
    highlightSlice,
    hoverSlice,
    coloringSlice,
    timingRangeSlice,
];
/**
 * Combines the slicers reducers manually, to account for leaving the rest
 * of the global reducer unchanged.
 */
export function dataflowReducer(state, action) {
    const [changedStates, stateChanged] = slices.reduce((prevValue, slice) => {
        const previousState = state[slice.name];
        const nextState = slice.reducer(previousState, action);
        if (nextState === previousState) {
            return prevValue;
        }
        return [Object.assign(Object.assign({}, prevValue[0]), { [slice.name]: nextState }), true];
    }, [[], false]);
    return stateChanged ? Object.assign(Object.assign({}, state), changedStates) : state;
}
export const dataflowInitialState = Object.fromEntries(slices.map((s) => [s.name, undefined]));
